* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.body {
  background-color: #c3f2f5;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
  display: flex;
}

#search-box {
  width: 100%;
  max-width: 300px;
  border: 2px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  font-size: 20px;
  display: block;
}

.country-list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  padding: 20px;
  list-style: none;
  display: flex;
}

.country-item {
  text-align: center;
  cursor: pointer;
  background-color: #f0f0f0;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  margin: 10px;
  padding: 10px;
  transition: all .2s ease-in-out;
  display: block;
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
}

.country-item:hover {
  transform: scale(1.05);
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

.country-name {
  margin-top: 10px;
  font-size: 18px;
  font-weight: bold;
  display: block;
}

.country-info {
  width: 100%;
  max-width: 1000px;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.country-info__thumb {
  max-width: 350px;
  text-align: center;
  margin-right: 50px;
}

.country-info__thumb img {
  margin-bottom: 20px;
}

.country-info__label {
  text-align: center;
  margin-top: 20px;
  font-size: 20px;
  font-weight: bold;
}

.country-info__text {
  justify-content: center;
  align-items: center;
  font-size: 15px;
  font-weight: normal;
  display: flex;
}

.notiflix-notify-icon {
  display: none;
}

